header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 5%;
  }
  header .c1 .h1{
    color: #024F9D;
    font-family: monospace;
    font-size: 3.5vw;
  }
  header .c1 .h2{
    color: #61B846;
    font-family: sans-serif;
    font-weight: bold;
    font-size: 3.5vw;
  }
  header .c1 h2,h6{
    margin: 0;
  }
  header h6 {
    color:gray
  }

  .categery1 {
    margin: 0 15%;
    display: flex;
    flex-direction: column;
  }
  .categery1 .head11 {
    margin-bottom: 5%;
  }
  .categery1 .cg-1 {
    
    display: flex;
    align-items: center;
  }
  .categery1 .content {
    width: 100%;
  }
  .categery1 .cont1 , .cont2 {
    display: flex;
    width: 100%;
    justify-content: space-between;
  }
  .categery1 .c1-logo img {
    width: 2vw;
  }
  .categery1 .cg-1 img {
    width: 20vw;
  }
  .categery1 .cg-1 .img {
    margin-right: 5%;
    margin-bottom: 5%;
  }
    .categery1 .incrementBtn{
    font-size: 1vw;
    width: 2vw;
    height: 2vw;
  }
  .categery1 .content a{
    text-decoration: none;
    color: grey;
  }
  .categery1 .cont2 button {
    width: 6vw;
    height: 6vw;
    border-radius: 15%;
    background-color: #61B846;
    border: 2px solid green;
  }
  .categery1 .cg-1 h2 , button {
    font-size: 3vw;
  }
  .categery1 .hr {
    width: 100%;
    background-color: grey;
    height: 2px;
    margin-bottom: 5%;
  }
  .categery1 .form {
    width: 100%;
    display: flex;
    justify-content: space-between;

  }
  .categery1 .form .h1 ,.h2{
    font-size: 3vw;
  }
  .categery1 .form .h2{
    color: #61B846;
  }