.container {
    display: flex;
    flex-direction: column;
  }
  .container header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 10%;
    margin-top: 5%;
  }
  .container header .c1 h2{
    color: #61B846;
    font-family: sans-serif;
    font-weight: bold;
    font-size: 4vw;
  }
  .container header .c1 h6{
    color: #024F9D;
    font-family: monospace;
    font-weight: bold;
    font-size: 2.5vw;
  }
  .container header .c1 h2,h6{
    margin: 0;
  }
  .container header h6 {
    color:gray
  }
  .container .c1-logo img{
    width: 4vw;
  }
  .container .main {
    margin: 0 10%;
    margin-top: 2%;
  
  }
  .container .main .grocery{
    width: 100%;
    margin-bottom: 3%;
  }
  .container .main .grocery img{
    width: 100%;
    margin-top: 5%;
  }
  /* search w3school */
  form.example input[type=text] {
    padding: 1vw 2vw;
    font-size: 17px;
    border: 1px solid grey;
    float: left;
    width: 76vw;
    border-right: none; /* Prevent double borders */
    background: #f1f1f1;
    border-radius: 10vw;
    margin-bottom: 2vw;
  }
  /* Style the submit button */
  /* form.example button {
    float: left;
