    font-size: 17px;
    border: 1px solid grey;
    border-left: none; /* Prevent double borders */
    /* cursor: pointer; */
    /* border-radius: 0px 50px 50px 0px; */
  
  /* } */
  form.example input:focus {
    outline: none;
  }
  form.example button:focus {
    outline: none;
  }
  /* Clear floats */
  form.example::after {
    content: "";
    clear: both;
    display: table;
  }
  .main .example .fa {
    color: black;
  }
  
  /* shoy by Categery */
  
  .slides {
    margin: 0 10%;
    margin-top: 2%;
    margin-bottom: 5%;
  }
  .cards {
    display: flex;
    overflow-x: scroll;
    gap:1rem
  }
  ::-webkit-scrollbar{
    display: none;
  }
  .slides .cards img {
    width: 15vw;
    height: 20vh;
  }
  
  .slides .cards p {
    text-align: center;
  }
  
  .categery {
    margin: 0 10%;
    display: flex;
    flex-direction: column;
    height: 70vh;
    overflow-y: scroll;
  }
  .categery .cg-1 {
    display: flex;
    align-items: center;
  }
  .categery .content {
    width: 100%;
  }
  .categery .cont1 , .cont2 {
    display: flex;
    width: 100%;
    justify-content: space-between;
  }
  .categery img {
    width: 20vw;
  }
  .categery .cont2 button {
    width: 6vw;
    height: 6vw;
    border-radius: 15%;
    background-color: #61B846;
    border: 2px solid green;
  }
  .cg-1 h2 , button {
    font-size: 3vw;
  }