.mainacc {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 3%;
  }
  .mainacc h1 {
    font-size: 3vw;
    margin-bottom: 2%;
  }
  .mainacc img {
    width: 13vw;
    margin-bottom: 1%;
  }
  .mainacc input {
    border: none;
    text-align: center;
    width: 35%;
    border-bottom: 2px solid black;
    font-size: 2.5vw;
  }
  .mainacc input:focus {
    outline: none;
  }
  .setting .or {
    margin: 0 20%;
    margin-top: 2%;
  }
  .setting .order {
    margin: 0 25%;
    margin-top: 2%;
  }
  .setting .status {
    display: flex;
    justify-content: space-between;
  }
  .setting .item p {
    margin: 0;
  }
  .setting .total {
    display: flex;
    justify-content: space-between;
    margin-top: 1%;
  }
  .line {
    margin-left: 10%;
    width: 80%;
    background-color: grey;
    height: 2px;
    margin-bottom: 5%;
  }
  .setting button {
    width: 30vw;
    height: 15vh;
    font-size: 3vw;
    background-color: lightseagreen;
    border-radius: 3vw;
    border: none;
    margin-left: 35%;
    text-align: center;
    margin-bottom: 2%;
  }