.btn {
    background-color: #61B846;
    color: white;
}
.main {
    display: flex;
    height: 100vh;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}
.first{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.first img {
    width: 30vw;
}
.first h1 {
    color: #61B846;
    font-size: 4vw;
}
.first p {
    color: #024F9D;
    font-size: 2vw;
}

.startbtn {
    border: none;
    background-color: #61B846;
    border-radius: 0.3rem;
    color: white;
    padding: 1vw 2vw;
    font-size: 2rem;
    font-weight: 500;
}
.second a{
    text-decoration: none;
    color: white;
    font-size: 2vw;
    font-weight: 600;
}
.link{
    display: flex;
    height: 20vh;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}
.link a{
    color: #024F9D;
    text-decoration: none;
}
.input{
    display: flex;
    width: 100%;
    border-bottom: solid 1px #D4D3D3;

}

.input input {
    width: 95%;
    border: none;
    padding: 1.5vw 0;
}
.input input::placeholder{
    color: #D4D3D3;
    font-size: 2vw;
}
.containerLogin{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    height: 100vh;
}
.containerLogin form{
    width: 50%;
}